@import url(https://fonts.googleapis.com/css2?family=Satisfy&family=Source+Code+Pro:wght@900&display=swap);
body {
  --main-text-color: #EEEEEE;
  --main-bg-color: hsla(212, 29%, 15%, 1);
  --secondary-bg-color: hsla(290, 50%, 35%, 0.75);
  margin: 0;
  font-family: 'Source Code Pro', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
  background-color: var(--main-bg-color);
  color: var(--main-text-color);
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100vh;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-header h1, p {
  margin: 0;
}

header.App-header  {
  padding: 0.5rem 0 0 0;
}

.bg-playing {
  animation: 4s linear bg-pulse-animation infinite;
 }
 
 @keyframes bg-pulse-animation {
   0% {
    box-shadow: 1px 1px 1px var(--main-text-color);
    -webkit-backdrop-filter: brightness(1.3);
            backdrop-filter: brightness(1.3);
    -webkit-filter: hue-rotate(0deg)
             blur(0px);
            filter: hue-rotate(0deg)
             blur(0px)
             /* brightness(1.1) */
             /* drop-shadow(0.1rem 0.1rem 0px var(--main-text-color)); */
   }
   50% {
    box-shadow: 0.5rem 0.5rem 0.5rem var(--main-text-color);
    -webkit-backdrop-filter: brightness(2.3);
            backdrop-filter: brightness(2.3);
    -webkit-filter: hue-rotate(5deg)
             blur(0px);
            filter: hue-rotate(5deg)
             blur(0px)
             /* brightness(1.8) */
             /* drop-shadow(0.5rem 0.5rem 0.5rem var(--main-text-color)); */
   }
   100% {
    box-shadow: 1px 1px 1px var(--main-text-color);
    -webkit-backdrop-filter: brightness(1.3);
            backdrop-filter: brightness(1.3);
    -webkit-filter: hue-rotate(0deg)
             blur(0px)
             brightness(1.1);
            filter: hue-rotate(0deg)
             blur(0px)
             brightness(1.1)
             /* drop-shadow(0.1rem 0.1rem 0px var(--main-text-color)); */
   }
 
 }
.drum-pad {
  border-radius: 0.5rem;
  box-sizing: border-box;
  padding: 10px;
  min-height: 20px;
  background-color: var(--secondary-bg-color);
  display: flex;
  flex-flow: column;
  align-items: center;
}

.drum-pad:focus {
  outline: 0;
}

.drum-pad header {
  font-size: 3rem;
}

.playing {
 animation: 4s linear glow-pulse-animation infinite;
}

@keyframes glow-pulse-animation {
  0% {
    -webkit-filter: hue-rotate(0deg)
            blur(0px)
            brightness(1.1)
            drop-shadow(0.1rem 0.1rem 0px var(--main-text-color));
            filter: hue-rotate(0deg)
            blur(0px)
            brightness(1.1)
            drop-shadow(0.1rem 0.1rem 0px var(--main-text-color));
  }
  50% {
    -webkit-filter: hue-rotate(5deg)
            blur(1px)
            brightness(1.8)
            drop-shadow(0.5rem 0.5rem 0.5rem var(--main-text-color));
            filter: hue-rotate(5deg)
            blur(1px)
            brightness(1.8)
            drop-shadow(0.5rem 0.5rem 0.5rem var(--main-text-color));
  }
  100% {
    -webkit-filter: hue-rotate(0deg)
            blur(0px)
            brightness(1.1)
            drop-shadow(0.1rem 0.1rem 0px var(--main-text-color));
            filter: hue-rotate(0deg)
            blur(0px)
            brightness(1.1)
            drop-shadow(0.1rem 0.1rem 0px var(--main-text-color));
  }

}

@media (min-width: 360px) {
  .drum-pad {
    padding: 30px;
    min-height: 100px;
  }

  .drum-pad header {
    font-size: 4rem;
  }

}
#display {
  /* background-color: hsla(0, 45%, 35%, 1); */
  /* box-sizing: border-box; */
  border: 2px inset var(--secondary-bg-color);
  border-radius: 0.5rem;
  grid-column: 1 / -1;
  min-height: 7rem;
  /* min-height: 10vmin;  */
  display: flex;
  flex-flow: column;
  justify-content: center;
  padding: 0.5rem;
  font-family: 'Satisfy', cursive;
  font-size: 1.7rem;
  font-weight: 400;
  -webkit-backdrop-filter: brightness(1.2);
          backdrop-filter: brightness(1.2);
}


#drum-machine-container {
  box-sizing: border-box;
  display: flex;
  flex-flow: column;
}

#drum-machine {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin: 0 1rem;
}

